<template>
    <div class="overview cus">
        <Steps :items="['', '', '']" @click="stepClick" :disabled="stepDisabled" :active="stepActive">
        </Steps>
        <SearchInputs @downHand="downHand" @searchHand="searchHand" :type="2" @add="addHand" :showAddBtn="true">
        </SearchInputs>
        <CusTableChange ref="table" :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"
            :checkBtn="roles_checkPerson"></CusTableChange>
        <!-- -->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="this.dialog==1" width="4.5rem"
            :before-close="handleClose">
            <EditTypeForm ref="form" :formDataChange="formData" :isEdit="formChangeEdit"></EditTypeForm>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="formChangeEdit" type="primary" @click="submit" :loading="loading"> </el-button>
                <el-button @click="dialog = 0"> </el-button>
            </span>
        </el-dialog>
        <!-- -->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="this.dialog==2" width="9rem"
            :before-close="handleClose">
            <div class="content-title"></div>
            <CusForm ref="CusForm" :editContentType="editContentType" :code="pointCode" :isEdit="true" :jpChecked="0">
            </CusForm>
            <div class="content-title"></div>
            <div class="upload-div">
                <Upload ref="upload" :jpChecked="0" :code="pointCode"></Upload>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submit" :loading="loading"> </el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
        <!-- -->
        <!---->
        <SelectUser :show="this.dialog==3" @selectPeople="selectPeople"></SelectUser>

        <!---->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="dialogVisible_change" width="4.5rem"
            :before-close="handleCloseDetailChange">
            <EditTypeForm :formDataChange="showFormData" :isEdit="false"></EditTypeForm>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCloseDetailChange"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="dialogVisible" width="9rem"
            :before-close="handleCloseDetail">
            <div class="content-title"></div>
            <CusForm :code="showPointCode" :isEdit="false" :jpChecked="jpChecked"></CusForm>
            <div class="content-title"></div>
            <FilesList :formData="showFormDetailData" :code="showPointCode" :jpChecked="jpChecked"></FilesList>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCloseDetail"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import TableCommon from "@/components/TableCommon.vue";
    import InfBox from "@/components/InfoBox.vue";
    import FilesList from "@/components/FilesList.vue";
    import CusTableChange from "./components/CusTableChange.vue";
    import Steps from "@/components/Steps.vue";
    import SearchInputs from "./components/SearchInputs.vue";
    import EditTypeForm from "./components/EditTypeForm.vue";
    import CusForm from "./components/CusForm.vue";
    import SelectUser from "./components/SelectUser.vue";
    import Upload from "./components/Upload.vue";
    //
    import {
        postJpointChange,
        getPointChangeList,
        getPointChangeCheck,
        postUpdatePoint,
        doCheckChangePoint,
        exportJpointChange,
        postJpointChangeUpdate,
    } from "@/api/archives.js"
    //
    import tool from "@/utils/tool.js";
    import {
        mapState
    } from 'vuex';
    import {
        ROLENAME
    } from "@/utils/config.js";
    export default {
        extends: TableCommon,
        name: 'overview',
        components: {
            InfBox, //
            CusTableChange, //
            Steps, //
            SearchInputs, //
            EditTypeForm, //
            CusForm, //
            SelectUser, //
            Upload, //
            FilesList //
        },
        data() {
            return {
                dialogVisible: false, //
                dialogVisible_change: false, //
                formDetailData: {}, // 
                showFormDetailData: {}, // 
                pointCode: '', //code
                showPointCode: '', //code
                formData: {}, // 
                showFormData: {}, //  
                formChangeEdit: true, //
                stepActive: 0, //
                dialog: 0, //  1  2  3 4
                loading: false, // loading

                stepDisabled: [], //
                editContentType: '1', // 
                jpChecked: '0', //    0 1
            }
        },
        computed: {
            ...mapState({ //
                roles_checkPerson: state => state.permission.roles.includes(ROLENAME.checkPerson),
                userId: state => state.user.userId, //user
            })
        },
        methods: {
            _getData(params = {}) { //    
                params = {
                    ...this.page,
                    ...this.search
                }
                getPointChangeList(params).then(res => {
                    const data = res.list ? res.list.rows : [];
                    this.tableData = data;
                    this.total = res.list.total;
                })
            },
            //
            stepClick(item) {
                switch (item) {
                    case '':
                        this.dialog = 1;
                        break;
                    case '':
                        this.dialog = 2;
                        // this.editContentType = row.content; //
                        break;
                    case '':
                        this.dialog = 3;
                        break;
                }
            },
            //
            btnClick(name, row) {
                this.editContentType = row.content; //
                if (name == '') {
                    this.toExam(row);
                    return;
                }

                if (row.content == 3) { // 
                    this.stepDisabled = [''];
                } else {
                    this.stepDisabled = [];
                }
                if (name == "") { //
                    this.showFormData = JSON.parse(JSON.stringify(row));
                    this.dialogVisible_change = true;
                    this.showPointCode = row.jpCode;

                } else if (name == "") { //
                    this.jpChecked = row.checkStatus * 1; //
                    this.dialogVisible = true;
                    this.showPointCode = row.jpCode;
                    this.showFormDetailData = JSON.parse(JSON.stringify(row));

                } else if (name == '') { // 
                    this.pointCode = row.jpCode; //id
                    this.formChangeEdit = false; // 
                    this.stepActive = 1;
                    this.dialog = 2;
                    this.changeId = row.id; //id
                    this.formData = JSON.parse(JSON.stringify(row));
                } else if (name == '') { //
                    this.pointCode = row.jpCode; //id
                    this.formChangeEdit = false; // 
                    this.stepActive = 2;
                    this.dialog = 3;
                    this.changeId = row.id; //id
                    this.formData = JSON.parse(JSON.stringify(row));
                }
            },
            //
            toExam(row) {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    distinguishCancelAndClose: true,
                    type: 'warning'
                }).then(() => {
                    this._check(row, 1)
                }).catch(action => {
                    if (action == 'close') return;
                    this._check(row, 0)
                });
            },
            //
            _check(row, isPass = 1) {
                doCheckChangePoint({
                    userId: this.userId,
                    changeId: row.id,
                    isPass,
                }).then(res => {
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                    this.reloadTable()
                })
            },
            //
            downHand() {
                exportJpointChange({
                    ...this.search,
                }).then(tool.down)
            },

            // 
            addHand() {
                if (this.stepActive != 0) {
                    this.clearFrom(); //
                    this.stepActive = 0; // 
                }
                this.dialog = 1; //
            },
            //
            handleClose(data) {
                this.dialog = 0;
            },
            //
            handleCloseDetail(data) {
                this.dialogVisible = false;
                this.clearFrom(true);
            },
            //
            handleCloseDetailChange(data) {
                this.dialogVisible_change = false;
                this.clearFrom(true);
            },
            //
            submit(data) {
                if (this.dialog == 1) { //
                    this.$refs['form'].submitForm((data) => {
                        this.loading = true;
                        const copy = {
                            ...data,
                        }
                        if (data.content == 3) { //  
                            copy.checkStatus = '4';
                        } else {
                            copy.checkStatus = '3';
                            if (data.jpCode) copy.ejpCode = data.jpCode;
                        }
                        postJpointChange(copy).then(res => {
                            this.pointCode = res.data.jpCode; //
                            this.changeId = res.data.id; //id

                            this.formChangeEdit = false; //
                            if (data.content == 3) {
                                this._nextStep(2); // 
                                this.stepDisabled = [''];
                                // 
                                this.stepActive = 2;
                            } else {
                                this._nextStep(); // 
                                this.stepActive = 1;
                                this.editContentType = data.content; //
                            }
                        }).catch(err => {
                            this.loading = false; //
                        })
                    })
                } else if (this.dialog == 2) { //
                    this.$refs['upload'].submitForm((filesData) => {
                        this.$refs['CusForm'].submitForm((data) => {
                            // return;
                            this._updatePoint({
                                ...data,
                                ...filesData
                            });
                            postJpointChangeUpdate({
                                id: this.changeId,
                                checkStatus: 4,
                            }).then(res => {})
                            if (this.stepActive === 1) this.stepActive = 2;
                        })
                    })
                }
            },
            // 
            selectPeople(user) {
                this.dialog = 0;
                if (!user) { //
                    return;
                }
                //
                this.loading = false; //
                getPointChangeCheck({
                    changeId: this.changeId, // id
                    userId: user.toString(),
                }).then(res => {
                    // 
                    this.loading = false; //
                    this.stepActive = 0; //
                    this.dialog = 0; //
                    this.$message({
                        title: '',
                        message: '',
                        type: 'success',
                        duration: 3000,
                    })
                    this.reloadTable(); //    
                    this.clearFrom();
                }).catch(err => {
                    this.loading = false; //
                })
            },
            //
            _updatePoint(opt) {
                //
                for (let key in opt) {
                    this.formDetailData[key] = opt[key];
                }
                //
                return postUpdatePoint(this.formDetailData).then(res => {
                    this.loading = false; //
                    if (res.data) {
                        this.loading = false; //
                        this._nextStep(); // 
                    }
                }).catch(err => {
                    this.loading = false; //
                })
            },
            //
            _delayAddDialog(stepNum) {
                const old = this.dialog;
                this.dialog = 0;
                setTimeout(() => {
                    this.dialog = old + stepNum;
                }, 1500)
            },
            //
            _nextStep(stepNum = 1) {
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success',
                    duration: 3000,
                });
                this.loading = false;
                this._delayAddDialog(stepNum); //
                this.reloadTable(); //
            },
            // 
            clearFrom(isShow) {
                if (isShow) {
                    this.showFormData = {};
                    this.showFormDetailData = {};
                    this.showPointCode = '';
                    return;
                }
                this.formData = {};
                this.formChangeEdit = true;
                this.pointCode = null;
                this.changeId = null;
                this.formDetailData = {};
                this.stepDisabled = [];
                this.stepActive = 0; //
            },
        }
    }
</script>

<style scoped lang="scss">
    .overview {
        margin-top: 0.24rem;

        .search {
            padding: 16px 0 0px 0;

            .li {
                &:last-child {
                    margin-bottom: 0;
                }

                display: flex;
                margin-bottom: 16px;

                .item {
                    width: 25%;
                    display: flex;
                    line-height: 32px;
                    height: 32;

                    .label {
                        min-width: 70px;
                        margin-right: 2px;
                    }

                    .rt {
                        flex: 1;
                    }

                    .btns {
                        width: 100%;
                        display: flex;
                        justify-content: flex-end;
                    }
                }
            }
        }

        /deep/ .item .el-input__inner {
            width: 270px;
            height: 32px;
            line-height: 32px;
        }

        .upload-div {
            padding: 0 40px;
        }
    }
</style>