<template>
    <div class="overview cus">
        <Steps :items="['', '', '']" :active="stepActive" @click="stepClick"></Steps>
        <SearchInputs @downHand="downHand" @searchHand="searchHand" @add="addHand" :showAddBtn="true"></SearchInputs>
        <CusTable ref="table" :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total"
            :checkBtn="roles_checkPerson"></CusTable>
        <!---->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="this.dialog==1" width="9rem"
            :before-close="handleClose">
            <div class="content-title"></div>
            <CusForm ref="CusForm" :isEdit="true" :code="pointCode" :formData="formDetailData" :jpChecked="0">
            </CusForm>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit"> </el-button>
                <el-button @click="dialog = 0"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="this.dialog==2" width="7rem"
            :before-close="handleClose">
            <Upload ref="upload" :formData="formDetailData"></Upload>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit"> </el-button>
                <el-button @click="dialog = 0"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <SelectUser :show="this.dialog==3" @selectPeople="selectPeople"></SelectUser>
        <!---->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="dialogVisible" width="8rem"
            :before-close="handleCloseDetail">
            <div class="content-title"></div>
            <CusForm1 :formData="showFormDetailData" :isEdit="false"></CusForm1>
            <div class="content-title"></div>
            <FilesList :formData="showFormDetailData"></FilesList>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCloseDetail"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import TableCommon from "@/components/TableCommon.vue";
    import FilesList from "@/components/FilesList.vue";
    import CusTable from "./components/CusTable.vue";
    import Steps from "@/components/Steps.vue";
    import SearchInputs from "./components/SearchInputs.vue";
    const CusForm = () => import('./components/CusForm.vue')
    const CusForm1 = () => import('./components/CusForm.vue')
    import Upload from "./components/Upload.vue";
    import SelectUser from "./components/SelectUser.vue";
    //
    import {
        postAddClient,
        postUpdateClient,
        postAddPoint,
        postUpdatePoint,
        doCheckPoint,
        exportRecord,
        getJpointlistTemp
    } from "@/api/archives.js";
    //
    import tool from "@/utils/tool.js";
    import {
        parseTime
    } from "@/utils/index.js";
    import {
        ROLENAME
    } from "@/utils/config.js";
    import {
        mapState
    } from 'vuex'
    export default {
        extends: TableCommon,
        name: 'overview',
        components: {
            CusTable, //
            Steps, //
            SearchInputs, //
            CusForm, //
            CusForm1: CusForm1, //
            Upload, //
            SelectUser, //
            FilesList, //
        },
        data() {
            return {
                dialogVisible: false, //
                formDetailData: {}, // 
                showFormDetailData: {}, //

                stepActive: 0, //
                dialog: 0, //  1  2  3 4
                pointCode: '', //code
            }
        },
        computed: {
            ...mapState({ //
                roles_checkPerson: state => state.permission.roles.includes(ROLENAME.checkPerson),
                userId: state => state.user.userId, //user
            })
        },
        methods: {
            _getData(params = {}) { //    
                params = {
                    ...this.page,
                    ...this.search
                }
                getJpointlistTemp(params).then(res => {
                    const data = res.list ? res.list.rows : [];
                    this.tableData = data;
                    this.total = res.list.total;
                })
            },
            //
            stepClick(item) {
                switch (item) {
                    case '':
                        this.dialog = 1;
                        break;
                    case '':
                        this.dialog = 2;
                        break;
                    case '':
                        this.dialog = 3;
                        break;
                }
            },
            //
            btnClick(name, row) {
                if (name == '') {
                    this.toExam(row);
                    return;
                }
                if (name == '') {
                    this.dialogVisible = true;
                    this.showFormDetailData = row;
                } else if (name == '') {
                    this.formDetailData = row;
                    this.pointCode = row.code;
                    this.stepActive = 1;
                    this.dialog = 2;
                } else if (name == '') {
                    this.formDetailData = row;
                    this.pointCode = row.code;
                    this.stepActive = 2;
                    this.dialog = 3;
                }
            },
            //
            toExam(row) {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    distinguishCancelAndClose: true,
                    type: 'warning'
                }).then(() => {
                    doCheckPoint({
                        userId: this.userId,
                        jpCode: row.code,
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                        this.reloadTable()
                    })
                }).catch(action => { //
                    if (action == 'close') return;
                    const opt = {
                        ...row
                    }
                    opt.checkStatus = '3'; //
                    this._updatePoint(opt, false).then(flag => {
                        if (flag) {
                            this.reloadTable()
                        }
                    }); //
                });
            },
            //
            downHand() {
                exportRecord({
                    datatype: 'new',
                    ...this.search,
                }).then(tool.down)
            },
            // 
            addHand() {
                //
                if (this.stepActive == 0) {
                    this.dialog = 1; //
                } else {
                    this.clearFrom(); //
                    this.oldFormDetailData = '';
                    this.dialog = 1; //
                    this.stepActive = 0; // 
                    this.$refs['table'].clearActiveRow(); //table
                }
            },
            //
            submit(data) {
                if (this.dialog === 1) { //
                    this.$refs['CusForm'].submitForm((data) => {
                        this.loading = true; //
                        this.formDetailData = { // 
                            ...data
                        };
                        if (data.code) {
                            if (this.formDetailData.checkStatus <= 3) {
                                data.checkStatus = '3'; // 
                            }
                            this._updatePoint(data).then(flag => {
                                if (flag) {
                                    if (this.stepActive === 0) this.stepActive = 1;
                                }
                            }); //
                        } else {
                            data.checkStatus = '3'; // 
                            //
                            postAddPoint(data).then(res => {
                                this.pointCode = res.data.code;
                                this.formDetailData.checkStatus = res.data.checkStatus;
                                this._nextStep(); // 
                                this.loading = false; //
                                if (this.stepActive === 0) this.stepActive = 1;
                            }).catch(err => {
                                this.loading = false; //
                            })
                        }
                    })
                } else if (this.dialog === 2) { //
                    this.$refs['upload'].submitForm((data) => {
                        this.loading = true; //
                        data.code = this.pointCode;
                        data.checkStatus = '4'; //
                        // 
                        this._updatePoint(data).then((flag) => {
                            //
                            if (flag) {
                                if (this.stepActive === 1) this.stepActive = 2;
                            }
                        });
                    })
                }
            },
            //
            selectPeople(user) {
                this.dialog = 0;
                if (!user) { //
                    return;
                }
                const opt = {
                    checkPerson: user.toString(),
                    code: this.pointCode,
                    checkStatus: '0',
                }
                this._updatePoint(opt, false).then((flag) => {
                    if (flag) {
                        this.clearFrom(); // 
                        //
                        this.stepActive = 0; //
                    }
                });
            },
            //
            _updatePoint(opt, stepAdd) {
                // 
                const copy = {
                    ...this.formDetailData,
                    ...opt,
                }
                //
                return postUpdatePoint(copy).then(res => {
                    this.loading = false; //
                    if (res.data) {
                        this.loading = false; //
                        this._nextStep(stepAdd); // 
                        //
                        for (let key in opt) {
                            this.formDetailData[key] = opt[key];
                        }
                        return true;
                    } else {
                        return false
                    }
                }).catch(err => {
                    this.loading = false; //
                    return false
                })
            },
            //
            handleClose(data) {
                this.dialog = 0;
            },
            //
            handleCloseDetail(data) {
                this.dialogVisible = false;
                this.clearFrom(true); //
            },

            //
            _delayAddDialog(delay = 1000) {
                const old = this.dialog;
                this.dialog = 0;
                if (old <= 2) {
                    setTimeout(() => {
                        this.dialog = old + 1;
                    }, delay)
                }
            },
            //
            /**
             * @stepAdd {boolean} 
             */
            _nextStep(stepAdd = true) {
                this.$notify({
                    title: '',
                    message: stepAdd ? '' : '',
                    type: 'success',
                    duration: 3000,
                });
                this.loading = false;
                stepAdd && this._delayAddDialog(); //
                this.reloadTable(); // 
            },
            //
            clearFrom(isShow = 0) { //isshow 
                if (isShow) {
                    this.showFormDetailData = {};
                    return;
                }
                this.stepActive = 0; //
                this.pointCode = '';
                this.formDetailData = {};
            },
        }
    }
</script>
<style scoped lang="scss">
    .overview {
        margin-top: 0.24rem;

        .search {
            padding: 16px 0 0px 0;

            .li {
                &:last-child {
                    margin-bottom: 0;
                }

                display: flex;
                margin-bottom: 16px;

                .item {
                    width: 25%;
                    display: flex;
                    line-height: 32px;
                    height: 32;

                    .label {
                        min-width: 70px;
                        margin-right: 2px;
                    }

                    .rt {
                        flex: 1;
                    }

                    .btns {
                        width: 100%;
                        display: flex;
                        justify-content: flex-end;
                    }
                }
            }
        }

        /deep/ .item .el-input__inner {
            width: 270px;
            height: 32px;
            line-height: 32px;
        }
    }
</style>