<template>
    <el-form :model="selectedObj" :rules="isEdit?rules:{}" ref="form"
        :class="isEdit?'form jpPoint':'form onlyShow jpPoint'" :disabled="!isEdit">
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="gsName">
                <div class="label"><span class="important">*</span></div>
                <el-select :disabled="otherDisable" v-model="selectedObj.gsName" filterable placeholder=""
                    @change="change_GS">
                    <el-option v-for="item in gs" :key="item.code" :label="item.name" :value="item.code">
                    </el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="glCode">
                <div class="label"><span class="important">*</span></div>
                <el-select :disabled="otherDisable" filterable @change="item => handleSelect(item,'glCode')"
                    v-model="selectedObj.glCode" placeholder="">
                    <el-option v-for="item in gl" :key="item.code" :label="item.name" :value="item.code">
                    </el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="kcCode">
                <div class="label"><span class="important">*</span></div>
                <el-select :disabled="otherDisable" v-model="selectedObj.kcCode" filterable placeholder=""
                    @change="change_KC">
                    <el-option v-for="item in kc" :key="item.code" :label="item.fullName" :value="item.code">
                    </el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item>
                <div class="label"></div>
                <el-autocomplete ref="autocomplete" @focus="autocompleteFocusFlag=true"
                    @blur="autocompleteFocusFlag=false" :disabled="otherDisable" v-model="selectedObj.kpName"
                    :fetch-suggestions="querySearchKp" placeholder="" @select="item => handleSelect(item,'kp')"
                    clearable @clear="clear"></el-autocomplete>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="from">
                <div class="label"><span class="important">*</span></div>
                <el-select :disabled="otherDisable" v-model="selectedObj.jfrom" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="name">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="otherDisable" v-model="selectedObj.name" placeholder="35kV101H1">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="pointType">
                <div class="label"><span class="important">*</span></div>
                <el-select :disabled="otherDisable" v-model="selectedObj.jlTy" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbBrand">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="otherDisable" v-model="selectedObj.dbBrand" placeholder=""></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbModel">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="otherDisable" v-model="selectedObj.dbModel" placeholder=""></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="dbCode">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="importantDisable" v-model="selectedObj.dbCode" placeholder=""></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="gwCode">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="importantDisable" v-model="selectedObj.gwCode" placeholder="">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item>
                <div class="label"></div>
                <el-input :disabled="importantDisable" v-model="selectedObj.ocode" placeholder="">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="gwNetcode">
                <div class="label"><span class="important">*</span>SIM</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.gwNetcode" placeholder="SIM">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="byRl">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="importantDisable" v-model="selectedObj.byRl" placeholder=""></el-input>
            </el-form-item>
        </div>

        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="hgCt">
                <div class="label"><span class="important">*</span>CT</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.hgCt" placeholder="CT"></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="hgPt">
                <div class="label"><span class="important">*</span>PT</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.hgPt" placeholder="PT"></el-input>
            </el-form-item>
        </div>

        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="dbRate">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="importantDisable" type="number" v-model="selectedObj.dbRate" placeholder="">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="gcAccount">
                <div class="label"><span class="important">*</span></div>
                <el-autocomplete ref="autocomplete1" @focus="autocompleteFocusFlag1=true"
                    @blur="autocompleteFocusFlag1=false" :disabled="otherDisable" v-model="selectedObj.gcAccount"
                    :fetch-suggestions="querySearchGc" placeholder="" @select="item => handleSelectGcAccount(item)"
                    clearable @clear="clear1"></el-autocomplete>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="xh">
                <div class="label"><span class="important">*</span></div>
                <el-select :disabled="otherDisable" v-model="selectedObj.status" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbDima">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="otherDisable" v-model="selectedObj.dbDima" placeholder=""></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbDimaTime">
                <div class="label"><span class="important">*</span></div>
                <el-date-picker :disabled="otherDisable" v-model="selectedObj.dbDimaTime" type="datetime"
                    placeholder="" default-time="12:00:00">
                </el-date-picker>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbLiveTime">
                <div class="label"><span class="important">*</span></div>
                <el-date-picker :disabled="otherDisable" v-model="selectedObj.dbLiveTime" type="datetime"
                    placeholder="" default-time="12:00:00">
                </el-date-picker>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="installTime">
                <div class="label"><span class="important">*</span></div>
                <el-date-picker :disabled="otherDisable" v-model="selectedObj.installTime" type="datetime"
                    placeholder="" default-time="12:00:00">
                </el-date-picker>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="gps">
                <div class="label"></div>
                <el-input :disabled="otherDisable" v-model="selectedObj.gps" placeholder=",">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item>
                <div class="label"></div>
                <el-input :disabled="otherDisable" v-model="selectedObj.place" placeholder="">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="installPerson">
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="otherDisable" v-model="selectedObj.installPerson" placeholder="">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item>
                <div class="label"><span class="important">*</span></div>
                <el-radio-group :disabled="otherDisable" v-model="selectedObj.isVr">
                    <el-radio label="1"></el-radio>
                    <el-radio label="0"></el-radio>
                </el-radio-group>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item>
                <div class="label"><span class="important">*</span></div>
                <el-input :disabled="otherDisable" class="textarea" type="textarea" v-model="selectedObj.remark"
                    placeholder="" :autosize="{ minRows: 2, maxRows:10 }"></el-input>
            </el-form-item>
        </div>
    </el-form>
</template>
<script>
    //
    import {
        getGlList,
        getKpList,
        getKcList,
        getGsList,
        getOrgList,
        getGcAccQueryByPage,
        getPointDetail,
        getGcAccQuery
    } from "@/api/archives.js";
    import {
        rules
    } from "./js/temp.js"
    import {
        parseTime,
        debounce
    } from "@/utils/index.js";
    import tool from "@/utils/tool.js";
    let nextYearDate = new Date();
    nextYearDate.setFullYear(nextYearDate.getFullYear() + 1);
    let init = {
        "name": "", //
        "byRl": 0, //
        "checkStatus": "", //(:1,:0)
        "code": "", //
        "dbBrand": "", //-
        "dbChecktime": new Date(), //
        "dbCode": "", //
        "dbDima": '0', //
        "dbDimaTime": new Date(), //
        "dbFrom": "", //
        "dbLiveTime": new Date(), //
        "dbModel": "", //
        "dbRate": 0, //
        "jfrom": "", //
        "gcAccount": "", //()
        "glCode": "", //
        "glName": "", //
        "gwCode": "", //
        "gwName": "", //
        "gwNetcode": "", //
        "hgCt": "", //ct
        "hgPt": "", //pt
        "installPerson": "", ////
        "installTime": new Date(), //
        "jlTy": "1", //12
        "kcCode": "", //
        "kpCode": "", //
        "kpName": "", //

        "ocode": "", //	
        "otable": "", //
        "pathDb": "", //	
        "pathDl": "", //
        "pathDs": "", //	
        "pathDy": "", //	
        "pathXc1": "", //
        "status": "", //	
        "gsName": '', //
        "ocode": '', //-
        "isVr": '0', //10
        "lx": '', //x
        "ly": '', //y
        "gps": '', //xy
        "place": '', //
        "remark": '', //
    }
    export default {
        props: {
            formData: Object,
            isEdit: { //
                type: Boolean,
                default: false,
            },
            editContentType: String, // 1 2: 
            code: String, //code
            jpChecked: {
                type: Number,
                default: 1,
            } //
        },
        data() {
            return {
                rules: {
                    name: [{ //  1.++2. +
                        validator: (rule, value, callback) => {
                            const {
                                glName,
                                kpName
                            } = this.selectedObj;
                            let outLineStr;
                            if (!glName) {
                                outLineStr = value.slice(value.lastIndexOf('') + 1);
                            } else {
                                outLineStr = value.split(glName)[1];
                            }
                            if (value.startsWith(' ')) {
                                callback(new Error(''));
                                return;
                            }
                            //
                            if (value === '') {
                                callback(new Error(''));
                                return;
                            }
                            //
                            if (!glName || (glName && !value.startsWith(glName))) {
                                if (!glName) {
                                    callback('');
                                } else {
                                    callback(new Error(`[${this.selectedObj.glName}],`));
                                }
                                return;
                            }
                            //
                            if (kpName && kpName !== '' && !outLineStr.startsWith(kpName)) {
                                callback(new Error(`[${this.selectedObj.kpName}],`));
                                return;
                            }
                            callback();
                        },
                        trigger: 'blur'
                    }],
                    gps: [{
                        validator: (rule, value, callback) => {
                            //
                            if (!value) {
                                callback();
                                return;
                            }
                            if (!value.includes(',')) {
                                callback(new Error(''));
                                return;
                            }
                            const ps = value.split(',');
                            if (!tool.isGpsLon(ps[0])) {
                                callback(new Error(''));
                                return;
                            }
                            if (!tool.isGpsLat(ps[1])) {
                                callback(new Error(''));
                                return;
                            }
                            callback();
                        },
                        trigger: 'blur'
                    }],
                    ...rules
                }, // 
                gl: [], // 
                kp: [], //
                kc: [], //
                gs: [], //
                selectedObj: {
                    ...init,
                },
                autocompleteFocusFlag: false,
                autocompleteFocusFlag1: false,
            }
        },
        mounted() {
            this.getData(); //
            this.querySearchGc = debounce(this.querySearchGc.bind(this), 300);
            this.querySearchKp = debounce(this.querySearchKp.bind(this), 300);
        },
        computed: {
            //
            importantDisable() {
                return this.isEdit && this.editContentType == 1
            },
            //
            otherDisable() {
                return this.isEdit && this.editContentType == 2
            }
        },
        methods: {
            //
            submitForm(success) {
                if (!this.isEdit) return;
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        if (this.code) this.selectedObj.code = this.code;
                        const copy = JSON.parse(JSON.stringify(this.selectedObj))
                        //
                        try {
                            copy.name = copy.name.trim();
                            copy.dbBrand = copy.dbBrand.trim();
                            copy.installPerson = copy.installPerson.trim();
                        } catch (e) {
                            console.log(e);
                        }
                        //
                        copy.dbChecktime = parseTime(new Date(copy.dbChecktime));
                        copy.dbDimaTime = parseTime(new Date(copy.dbDimaTime));
                        copy.dbLiveTime = parseTime(new Date(copy.dbLiveTime));
                        copy.installTime = parseTime(new Date(copy.installTime));
                        if (copy.gps) {
                            copy.lx = copy.gps.split(',')[0];
                            copy.ly = copy.gps.split(',')[1];
                        }
                        success(copy);
                    } else {
                        return false;
                    }
                });
            },
            //
            querySearchKp(keyWord, cb) {
                const opt = {
                    pageNum: 1,
                    pageSize: 20,
                    kcCode: this.selectedObj.kcCode,
                    _loading: false, //
                }
                if (keyWord == '') {
                    this.selectedObj.kpCode = '';
                    this.selectedObj.kpName = '';
                }
                if (keyWord !== '') {
                    opt.keyWord = keyWord;
                }
                getKpList(opt).then(res => {
                    if (res.data) {
                        const list = res.data.map(item => ({
                            ...item,
                            value: item.name,
                        }))
                        cb(list);
                        if (list.length == 0) {
                            this.selectedObj.kpCode = '';
                            this.selectedObj.kpName = '';
                        }
                    } else {
                        cb([]);
                    }
                })
            },
            //
            querySearchGc(keyWord, cb) {
                const opt = {
                    pageNum: 1,
                    pageSize: 10,
                    _loading: false, //
                }
                if (keyWord == '') {
                    this.selectedObj.gcAccount = '';
                }
                if (keyWord !== '') {
                    opt.gcAcc = keyWord;
                }
                // if (keyWord.length < 2) return cb([]);
                getGcAccQuery(opt).then(res => {
                    if (res.data) {
                        const list = res.data.map(item => ({
                            ...item,
                            value: `${item.gcAccount}${item.payer}`,
                        }))
                        cb(list);
                        if (list.length == 0) {
                            this.selectedObj.gcAccount = '';
                        }
                    } else {
                        cb([]);
                    }
                })
            },
            getData() {
                this._updateGl();
                this._updateKc();
                this._updateGs();
            },
            handleSelectGcAccount(item) {
                this.selectedObj.gcAccount = item.gcAccount;
                // this.$refs['form'].validateField('gcAccount');
            },
            // 
            change_KC(data) {
                const opt = {
                    kcCode: data
                }
                this.selectedObj.kpCode = ''; //
                this.selectedObj.kpName = ''; //
                this.$refs['form'].validateField('kpCode');
            },
            // 
            change_GS(data) {
                const opt = {
                    gsCode: data
                }
                this._updateGl(data === 'all' ? {} : opt);
                this.selectedObj.glCode = ''; //
                this.$refs['form'].validateField('gsName');
            },
            //
            _updateGl(params) {
                getGlList(params).then(data => {
                    this.gl = data.data;
                })
            },
            //
            _updateKp(params) {
                getKpList(params).then(data => {
                    this.kp = data.data;
                })
            },
            //
            _updateKc(params) {
                getKcList(params).then(data => {
                    this.kc = data.data;
                })
            },
            //
            _updateGs(params) {
                getGsList(params).then(data => {
                    this.gs = data.data;
                })
            },
            handleSelect(item, prop) {
                if (prop == 'kp') {
                    this.selectedObj.kpCode = item.code;
                    this.$refs['form'].validateField('kpCode');
                } else if (prop == 'glCode') {
                    this.selectedObj.glName = this.gl.find(n => n.code == item).name;
                    this.$refs['form'].validateField('glCode');
                    //
                    getGcAccQueryByPage({
                        glCode: item,
                    }).then(res => {
                        if (res.rows && res.rows.length) {
                            this.selectedObj.gcAccount = res.rows[0].gcAccount;
                        } else {
                            this.selectedObj.gcAccount = '';
                        }
                    })
                } else {
                    this.$refs['form'].validateField(prop);
                }
                if (prop == 'kp' || prop == 'glCode') {
                    if (!this.selectedObj.name) {
                        this.selectedObj.name = this.selectedObj.glName + (this.selectedObj.kpName == '' ? '' : this
                            .selectedObj.kpName);
                    } else if (this.selectedObj.name == this.selectedObj.glName || this.selectedObj.name == this
                        .selectedObj.kpName) {
                        this.selectedObj.name = this.selectedObj.glName + (this.selectedObj.kpName == '' ? '' : this
                            .selectedObj.kpName);
                    }
                    this.$refs['form'].validateField('name');
                }
            },
            getById(code) {
                getPointDetail({
                    jpcode: code,
                    checked: this.jpChecked,
                }).then(res => {
                    const obj = res.data;
                    this.reset();
                    if (obj) {
                        this._dealWithData(obj); //
                    }
                })
            },
            //
            _dealWithData(obj) {
                for (let key in obj) {
                    this.selectedObj[key] = obj[key];
                }
                this.selectedObj.isVr = this.selectedObj.isVr + '';
                if (this.selectedObj.lx) {
                    this.selectedObj.gps = this.selectedObj.lx + ',' + this.selectedObj.ly;
                } else {
                    this.selectedObj.gps = !this.isEdit ? '' : '';
                }
                if (!this.isEdit) {
                    this.selectedObj.place = !this.selectedObj.place ? '' : this.selectedObj.place;
                    this.selectedObj.remark = !this.selectedObj.remark ? '' : this.selectedObj.remark;
                }
            },
            reset() {
                for (let key in init) {
                    this.selectedObj[key] = init[key];
                }
                this.$refs['form'] && this.$refs['form'].clearValidate();
            },
            clear() {
                if (this.autocompleteFocusFlag) this.$refs.autocomplete.activated = true;
            },
            clear1() {
                if (this.autocompleteFocusFlag1) this.$refs.autocomplete1.activated = true;
            }
        },
        watch: {
            formData: {
                handler(val) {
                    if (!val) return;
                    this.getData(); //
                    if (Object.keys(val).length === 0) {
                        this.reset();
                        return;
                    }
                    this._dealWithData(val); //
                    this.$nextTick(() => {
                        if (this.isEdit) {
                            this.$refs['form'] && this.$refs['form'].validate()
                        }
                    })
                },
                deep: true,
                immediate: true,
            },
            code: {
                handler(val) {
                    if (!val) return;
                    this.getData(); //
                    this.getById(val);
                },
                immediate: true,
            },
        }
    }
</script>

<style scoped lang="scss">
    .form {
        display: flex;
        flex-flow: row wrap;

        .item {
            width: 50%;
            margin-bottom: 22px;
        }

        /deep/ .el-form-item__error {
            width: 100%;
        }

        &.onlyShow {
            /deep/ .el-textarea__inner {
                background-color: transparent;
                border: 0;
            }
        }

        .item {
            &.disable {

                /deep/ .el-input.is-disabled .el-input__inner,
                /deep/ .el-range-editor.is-disabled {
                    background-color: rgba(46, 74, 247, 0.03);
                    color: #999;
                }

                /deep/ .el-range-editor.is-active {
                    border-color: #E4E7ED;
                }


            }

            display: flex;
            line-height: 32px;
            height: 32;

            .label {
                min-width: 119px;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .rt {
                flex: 1;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }
        }

        .important {
            color: #FF2B73;
        }

        /deep/ .item .el-input__inner,
        /deep/ .el-textarea__inner {
            width: 270px;
            height: 32px;
            line-height: 32px;
        }

        /deep/ .el-form-item {
            margin-bottom: 0;
        }

        /deep/ .el-form-item__content {
            display: flex;
            line-height: 32px;
        }

        /deep/ .el-radio-group {
            padding-top: 8px;
        }
    }
</style>