<!---->

<template>
    <div style="width: 100%" class="cus-table">
        <el-table v-table-scroll style="width: 100%" :data="tableData" :row-class-name="tableRowClassName"
            :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column type="index" label="" width="90" />
            <el-table-column label="" prop="kcName" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="kpName" width="150" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="glName" width="200" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="name" width="240" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="jlTy" width="120">
                <template slot-scope="scope">
                    <span>{{jlType[scope.row['jlTy']] }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" prop="jfrom" width="80">
                <template slot-scope="scope">
                    <span>{{scope.row['jfrom'] }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" prop="status" width="80">
                <template slot-scope="scope">
                    <span :class="scope.row['status']===''?'green':'red'">{{scope.row['status'] }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" prop="checkStatus" width="120">
                <template slot-scope="scope">
                    <span
                        :class="scope.row['checkStatus']==='1'?'green':'red'">{{statusArr[scope.row['checkStatus']!==null?scope.row['checkStatus']:0]}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="left" fixed="right" class-name="cusTable" width="90">
                <template slot-scope="scope">
                    <span v-if="scope.row.checkStatus==3||scope.row.checkStatus==4" class="cus-btn"
                        @click="handClick(getLabel(scope.row),scope.row)" type="text" size="small">
                        {{getLabel(scope.row)}}
                    </span>
                    <span v-else-if="scope.row.checkStatus==1" class="cus-btn" @click="handClick('',scope.row)"
                        type="text" size="small"></span>
                    <el-dropdown v-else @command="handleCommand($event,scope.row)">
                        <span class="el-dropdown-link cus-btn">
                            <i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <!-- <el-dropdown-item command=""></el-dropdown-item> -->
                            <el-dropdown-item command=""></el-dropdown-item>
                            <el-dropdown-item v-if="checkBtn" command=""></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import {
        mapState
    } from 'vuex'
    import Table from "@/components/ElTableCommon.vue";
    export default {
        extends: Table,
        name: 'table1',
        props: {
            checkBtn: { //
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                jlType: ['', '', '', ''],
                statusArr: ['', '', '', '', ''],
                replayId: '',
            }
        },
        computed: {
            ...mapState({ //
                userId: state => state.user.userId, //user
            })
        },
        methods: {
            getLabel(row) { // 
                if (row.checkStatus === '3') {
                    return '';
                } else if (row.checkStatus === '4') {
                    return '';
                }
            }
        }
    }
</script>
<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding: 0 2px;
        cursor: pointer;
    }

    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>