<!--Stepsdoc-->
<!--
  
 <Steps :items="['', '', '', '']" @click="stepClick"></Steps>


title: 
items{array}   ['1','2','3'],

@click:{string} 
-->

<template>
    <div class="over-box">
        <el-steps :active="active" style="width:70%" align-center>
            <el-step v-for="item in items" :key="item" :class="disabled.includes(item)?'disabled':''" :title="item"
                @click.native="click(item)">
                <span class="cricle" :title="item" slot="icon"></span>
            </el-step>
        </el-steps>
    </div>
</template>
<script>
    export default {
        props: {
            items: {
                type: Array,
                default: () => {
                    return ['', '', '', '']
                }
            },
            disabled: {
                type: Array,
                default: () => {
                    return []
                }
            }, //
            active: {
                type: Number,
                default: 1,
            }
        },
        methods: {
            click(item) {
                // 
                if (this.items.indexOf(item) >= this.active + 1 || this.disabled.includes(item)) return;
                this.$emit('click', item)
            }
        }
    };
</script>
<style scoped lang="scss">
    .over-box {
        background: rgba(46, 74, 247, 0.03);
        border-radius: 2px;
        border: 1px solid rgba(46, 74, 247, 0.31);
        padding: 10px 20px;
        color: #333;
        display: flex;
        justify-content: center;
    }

    .cricle {
        display: inline-block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #2E4AF7;
    }

    .disabled {
        /deep/ .el-step__title {
            text-decoration: line-through;
            cursor: not-allowed;
        }

        /deep/ .el-step__title.is-wait:hover,
        /deep/ .el-step__title.is-process:hover {
            color: #909CB1 !important;
        }

        /deep/ .el-step__title.is-finish:hover {
            color: #2E4AF7 !important;
        }
    }
</style>