<!--table -->
<script>
    import {
        debounce
    } from "@/utils/index.js"
    export default {
        name: 'TableCommon',
        data() {
            return {
                dialogShow: false,
                loading: false,
                //
                tableData: [], //
                total: 0, //
                page: { // 
                    pageNum: 1, //
                    pageSize: 10,
                },
                search: {}, // 
            }
        },
        mounted() {
            //
            if (this._getData) {
                this._getData = debounce(this._getData.bind(this), 100)
                this._getData(this.page);
            }
        },
        methods: {
            handleClose() {
                this.dialogShow = false;
            },
            //
            handleSizeChange(data) {
                if (this.page.pageSize !== data) {
                    this.page.pageSize = data;
                    this._getData();
                }
            },
            //
            handleCurrentChange(data) {
                if (this.page.pageNum !== data) {
                    this.page.pageNum = data;
                    this._getData();
                }
            },
            //
            reloadTable() {
                this._getData();
            },
            //
            searchHand(data) {
                this.page.pageNum = 1; //1
                this.search = data;
                this._getData();
            }, //
        },
        activated: function () {
            // 
            this._getData();
        },
        deactivated: function () {
            // 
            this.tableData = [];
        }
    }
</script>