<!--CusTabledoc-->
<!--
  src\views\archives\calcPoint\overview.vue  
<CusTable :tableRows="tableRows" :tableData="tableData" :tableBtns="btnsColumns"
    :btnsColumnWidth="btnsColumnWidth" :btnsColumnName="btnsColumnName"
    @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></CusTable>

:
tableRows: {Array} 
tableData: {Array} data  
tableBtns: {Array}   [{label: ''}] 
btnsColumnWidth: {Number}  100 
btnsColumnName: {String}  
@handleSizeChange: {String} 
@handleCurrentChange: {String} 

-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table style="width: 100%" :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
            :row-class-name="tableRowClassName">
            <el-table-column type="index" label="" width="90" />
            <el-table-column label="" prop="kcName" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="kpName"></el-table-column>

            <el-table-column label="" prop="jpName" width="250" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="content">
                <template slot-scope="scope">
                    <span>{{content[scope.row['content']] }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" prop="cause" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="checkStatus" width="120">
                <template slot-scope="scope">
                    <span
                        :class="scope.row['checkStatus']==='1'?'green':'red'">{{statusArr[scope.row['checkStatus']!==null?scope.row['checkStatus']:0]}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="left" fixed="right" class-name="cusTable" width="90">
                <template slot-scope="scope">

                    <span v-if="scope.row.checkStatus==3||scope.row.checkStatus==4" class="cus-btn"
                        @click="handClick(getLabel(scope.row),scope.row)" type="text" size="small">
                        {{getLabel(scope.row)}}
                    </span>
                    <el-dropdown v-else @command="handleCommand($event,scope.row)">
                        <span class="el-dropdown-link cus-btn">
                            <i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command=""></el-dropdown-item>
                            <el-dropdown-item v-if="scope.row.content==1||scope.row.content==2" command="">
                            </el-dropdown-item>
                            <el-dropdown-item v-if="checkBtn&&scope.row.checkStatus==0" command="">
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>

</template>

<script>
    export default {
        name: 'table1',
        props: {

            tableData: { //
                type: Array,
                default: () => []
            },
            total: {
                type: Number,
                default: 0
            },
            pageSize: { //
                type: Number,
                default: 10
            },
            checkBtn: { //
                type: Boolean,
                default: false
            },
            pageNum: {
                type: Number,
                default: 1
            },

        },

        data() {
            return {
                content: ['', '', '', ''],
                statusArr: ['', '', '', '', ''],
                replayId: '',
            }
        },
        created() {

        },

        mounted() {

        },
        methods: {
            tableRowClassName({
                row,
                rowIndex
            }) {
                if (row.id == this.replayId) {
                    return 'active-row';
                }
                return '';
            },
            handleSizeChange(data) {
                this.$emit('handleSizeChange', data)
            },
            handleCurrentChange(data) {
                this.$emit('handleCurrentChange', data)
            },
            getLabel(row) { // 
                if (row.checkStatus === '3') {
                    return '';
                } else if (row.checkStatus === '4') {
                    return '';
                }
            },
            handClick(textName, row) {
                this.$emit('btnClick', textName, row);
                this.replayId = row.id.toString(); //
            },
            handleCommand(item, row) {
                this.$emit('btnClick', item, row);
                this.replayId = row.id.toString(); //
            }
        }
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
        margin-top: 15px;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding: 0 2px;
        cursor: pointer;
    }
</style>